/* Carousel Custom Brands --------------- */
#brandsCarousel .owl-item{
    padding:15px;
}
#brandsCarousel .owl-prev::before,#brandsCarousel .owl-next::before{
    background-color: #e1e1e1;
}
 /* ===================================
    Owl carousel
====================================== */
.main-content-owlcarousel {
  position: relative;
}
.main-content-owlcarousel .owl-theme .owl-nav {
  position: absolute;
  top: 25%;
  left: 0;
  right: 0;
}
.main-content-owlcarousel .owl-theme .owl-nav .owl-prev, 
.main-content-owlcarousel .owl-theme .owl-nav .owl-next {
  position: absolute;
  color: inherit;
  background: none;
  border: none;
  z-index: 100;
}
.main-content-owlcarousel .owl-theme .owl-nav .owl-prev i, 
.main-content-owlcarousel .owl-theme .owl-nav .owl-next i {
  font-size: 2.5rem;
  color: #cecece;
}
.main-content-owlcarousel .owl-theme .owl-nav .owl-prev {
  left: 0;
}
.main-content-owlcarousel .owl-theme .owl-nav .owl-next {
  right: 0;
}
.main-content-owlcarousel .owl-theme .owl-nav .owl-prev,
.main-content-owlcarousel .owl-theme .owl-nav .owl-next{
	font-size: 2rem;
	padding: 0 1rem !important;
	opacity: 0;
	background-color: #869791;
	color: #fff;
}
.main-content-owlcarousel:hover .owl-theme .owl-nav .owl-prev,
.main-content-owlcarousel:hover .owl-theme .owl-nav .owl-next{
	opacity: 1;
}

/* Buy Page Form ------------- */
.wrap-box-main,
.bg-left-box,
.bg-right-box{
	position: relative;
	width: 100%;
}
.wrap-box-main{
	display: flex;
	flex-wrap: wrap;
}
.bg-left-box,
.bg-right-box{
	padding: 3rem;
}
/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
	.bg-left-box,
	.bg-right-box{
		-ms-flex: 0 0 50%;
		flex: 0 0 50%;
		max-width: 50%;
	}
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
	.bg-left-box,
	.bg-right-box{
		-ms-flex: 0 0 50%;
		flex: 0 0 50%;
		max-width: 50%;
	}
}
.bg-left-box{
	background-color: #eaeaea;
}
.bg-right-box{
	background-color: #fafafa;
	color: #777;
	flex-direction: column;
}
.flex-d-col{
	flex-direction: column;
}

.preloader-loading{
	position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: url(https://cdn.vnoc.com/icons/loading-128x128.gif);
    background-repeat: no-repeat;
    background-position: center;
    background-color: #fff;
}

/* 404 CSS STYLE --------------------------- */
#notfound{
    position:relative;
    height:100vh
}
#notfound .notfound{
    position:absolute;
    left:50%;
    top:50%;
    -webkit-transform:translate(-50%,-50%);
    -ms-transform:translate(-50%,-50%);
    transform:translate(-50%,-50%)
}
.notfound{
    max-width:560px;
    width:100%;
    padding-left:160px;
    line-height:1.1
}
.notfound .notfound-404{
    position:absolute;
    left:0;
    top:0;
    display:inline-block;
    width:140px;
    height:140px;
    background-image:url('https://cdn.vnoc.com/icons/contrib/emoji.png');
    background-size:cover
}
.notfound .notfound-404:before{
    content:'';
    position:absolute;
    width:100%;
    height:100%;
    -webkit-transform:scale(2.4);
    -ms-transform:scale(2.4);
    transform:scale(2.4);
    border-radius:50%;
    background-color:#f2f5f8;
    z-index:-1
}
.notfound h1{
    font-family:nunito,sans-serif;
    font-size:65px;
    font-weight:700;
    margin-top:0;
    margin-bottom:10px;
    color:#151723;
    text-transform:uppercase
}
.notfound h2{
    font-family:nunito,sans-serif;
    font-size:21px;
    font-weight:400;
    margin:0;
    text-transform:uppercase;
    color:#151723
}
.notfound p{
    font-family:nunito,sans-serif;
    color:#999fa5;
    font-weight:400
}
@media only screen and (max-width:767px){
    .notfound .notfound-404{
        width:110px;
        height:110px
    }
    .notfound{
        padding-left:15px;
        padding-right:15px;
        padding-top:110px
    }
}

