/* Nav Tabs - Buy Form Page - Our Process ------------------------- */
.circle-step-main {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    text-align: center;
}
.circle-step-main span {
    display: block;
}

.nav.nav-tabs.process-nav-tab{
	display: block;
	border-bottom: none;
	position: relative;
	text-align: center;
	margin-bottom: 1rem;
}
.nav.nav-tabs.process-nav-tab:before{
	content: "";
    display: block;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid #ddd;
    position: absolute;
    top: 50%;
}
.nav.nav-tabs.process-nav-tab .nav-item{
	display: inline-block;
	position: relative;
	z-index: 2;
	margin-right: 15px;
}

.nav.nav-tabs.process-nav-tab .nav-item a{
	display: block;
	border-radius: 100%;
    box-shadow: 0 0 30px 0 transparent;
    border: none;
    color: #c7c7c7;
    height: 100px;
    width: 100px;
    background-color: #f5f5f5;
}

.nav.nav-tabs.process-nav-tab .nav-item a.active{
	border: none;
    background-color: #fff;
    box-shadow: 0 0 30px 0 rgba(0, 0, 0, .1);
    color: #000;
    text-align: center;
}